<template >
    <div style="display:flex">
        <div id="drawing"></div>
        <el-tree :data="data"
            ref="tree"
            node-key="label"
            :props="defaultProps"
            @node-click="handleNodeClick"></el-tree>
    </div>

</template>

<script>
import aaa from '@/assets/aaa.svg'
export default {
    data() {
        return {
            data: [{
                label: '一级 1',
                children: [{
                    label: '二级 1-1',
                    children: [{
                        label: '三级 1-1-1'
                    }]
                }]
            }, {
                label: '一级 2',
                children: [{
                    label: '二级 2-1',
                    children: [{
                        label: '三级 2-1-1'
                    }]
                }, {
                    label: '二级 2-2',
                    children: [{
                        label: '三级 2-2-1'
                    }]
                }]
            }, {
                label: '一级 3',
                children: [{
                    label: '二级 3-1',
                    children: [{
                        label: '三级 3-1-1'
                    }]
                }, {
                    label: 'bbb',
                    children: [{
                        label: '三级 3-2-1'
                    }]
                }]
            }],
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            aaa: aaa,
        }
    },
    methods: {

        handleNodeClick(node) {
            console.log(node)
         const elements = SVG.select('g')
           for (const t of elements.members) {
            if (t.node.id == node.label) {
                console.log(t);
                t.move(200, 350)
           
            }
        }
        }
    },
    mounted() {
        // http://192.168.66.221:8080/FileLibrarys/FileLibrary/test/FullRobot/FullRobot.icon.svg
         window.draw = SVG('drawing').size(500, 500).viewbox(0, 0, 1300, 900);
        // const a =  url('http://192.168.66.221:8080/FileLibrarys/FileLibrary/test/FullRobot/FullRobot.icon.svg')
        console.log(draw)

        // for (const t of elements) {
        //     if (t.id != undefined) {
        //         console.log(t);
        //     }
        // }
        draw.svg(aaa)
        const elements = SVG.select('g')
        console.log(this.$refs.tree)
        // console.log(elements);
        const _this = this;
        for (const t of elements.members) {
            if (t.node.id !== '') {
                console.log(t);
                // t.move(200, 350)
                t.click(function () {
                    this.fill({ color: '#f06' })
                    _this.$refs.tree.setCurrentKey(t.node.id)
                })
            }
        }
    }
}
</script>

